const COMPANY_LEVEL_PROJECT_ID = 1;
const HOME_PAGE_ID = '-1000';

export class NavUrlHandler {

    constructor(menuItem, projectId) {
        this.menuItem = menuItem;
        this.projectId = projectId;
        this.menuItemUrl = '';
        this.historyName = '';
    }

    handle() {
        //Handle url and history name
        this.menuItemUrl = this.menuItem.url;
        this.historyName = '../tririga#' + this.menuItem.name;
        if (this.projectId && this.projectId > COMPANY_LEVEL_PROJECT_ID) {
            //Add history hash if doesn't already exist here.
            if (this.menuItemUrl.indexOf('#') < 0) {
                this.menuItemUrl = this.menuItemUrl + '#'
            }
            let projectContextUrl = this._handleProjectContextUrl();
            this.menuItemUrl = this.menuItemUrl + projectContextUrl;
            this.historyName = this.historyName + projectContextUrl;
        }
    }

    _handleProjectContextUrl() {
        let projectQueryString = this.menuItem.id == HOME_PAGE_ID? 'project=': '&project=';
        return projectQueryString + this.projectId;
    }

    getMenuItemUrl() {
        return this.menuItemUrl;
    }

    getHistoryName() {
        return this.historyName;
    }

}