import { List } from "tririga-carbon-addons-iot-react";
import React, { Component } from 'react';
import {AppMsg} from '../utils';

class Sitemap extends Component {

  constructor(props) {
    super(props);
    this.state = {
      sitemapMenuItemIds: []
    }
    this.shouldHandleSelect = true;
  }

  componentDidUpdate(prevProps) {
    if (prevProps.sitemapMenuItemIdsToDisplay !== this.props.sitemapMenuItemIdsToDisplay) {
      this.setState({
        sitemapMenuItemIds: this.props.sitemapMenuItemIdsToDisplay
      });
    }
  }

  _selectSitemapMenuItem(id) {
    if (this.shouldHandleSelect) {
      this.props.sitemapMenuItemButtonClick(id);
    }
    this.shouldHandleSelect = true;
  }

  _toggleSitemapMenuItem(id) {
    //We shouldn't handle select  on expand/collapse toggle.
    this.shouldHandleSelect = false;
    if (this.state.sitemapMenuItemIds.filter(rowId => rowId === id).length > 0) {
      this.setState({
        sitemapMenuItemIds: this.state.sitemapMenuItemIds.filter(rowId => rowId !== id)
      });
    } else {
      this.setState({
        sitemapMenuItemIds: this.state.sitemapMenuItemIds.concat([id])
      });
    }
  }
  
  render() {
    return (
        <div style={{ width: '100%' }}>
      <List
            hasMultiSelect={false}
            hasPagination={false}  
            hasSearch
            i18n={{
              close: AppMsg.getMessage("CLOSE"),
              expand: AppMsg.getMessage("EXPAND"),
              searchPlaceHolderText: AppMsg.getMessage(AppMsg.PROJ_SEARCH)
            }}
            isFullHeight={false}
            isLoading={false}
            items={this.props.sitemapMenuItemsToDisplay}
            handleSelect={(id) => {this._selectSitemapMenuItem(id)}}
            title={<span className="no-break">{this.props.currentSelectedMenuItemLabel}</span>}
            expandedIds={this.state.sitemapMenuItemIds}
            toggleExpansion={(id) => {
              this._toggleSitemapMenuItem(id);
            }}
        />
    </div>
    );
  }
}

export default Sitemap;