import {HierarchyListItemHandler} from "../handlers/HierarchyListItemHandler";

export class ProjectContextService {

    constructor(navHandler, projectContainer) {
        this.navHandler = navHandler;
        this.projectContainer = projectContainer;
    }

    handleProjectSwitcherMenu() {
    fetch('../../ui/navContext/projectSwitcherList')
        .then((resp) => resp.json()) 
        .then(respData => {
            this._processProjectSwitcherPortals(respData);
        });
    }

    switchProject(subCatId, projectId, menuItemName, isFromBrowserAction, navHandleCallback) {

        fetch('../../ui/navContext/projectSwitch?subCatId='+subCatId+'&projectId='+projectId)
        .then((resp) => resp.json())
        .then(data => {
            //Show project side nav if not company project.
            this.handleShowProjectMenu(data.projectId, menuItemName, isFromBrowserAction);
    
            this.handleProjectContextProperties(data.projectId, 
                                                data.projectName, 
                                                data.priorProjectId, 
                                                data.priorProjectName);
            
            if (navHandleCallback) {
              navHandleCallback();
            }
        });
    }

    handleProjectContextProperties(projectId, projectName, priorProjectId, priorProjectName) {
        this._getCurrentProjectContainer().handleProjectState(projectId, 
            projectName, 
            priorProjectId, 
            priorProjectName,
            this.shouldShowRecentProject(projectId, priorProjectId));
    }

    handleShowProjectMenu(projectId, menuItemName, isFromBrowserAction) {
        //Show project side nav if not company project.
        if (this._getCurrentProjectContainer().isProjectLevelContext(projectId)) {
           
          //Project Nav Items
          fetch('../../api/v1/navCollection/currentUser')
          .then((resp) => resp.json()) 
          .then(data => {
            let projectNavItems = [];
            let navItems = data.navItems;
            this._getCurrentProjectContainer().projectContainerHierarchyListItemHandler = new HierarchyListItemHandler();
            this._getCurrentProjectContainer().projectContainerHierarchyListItemHandler.handleFromProjectSubMenu(projectNavItems, navItems);
            this._getCurrentProjectContainer().handleProjectMenuItemsState(projectNavItems);
            this._getCurrentProjectContainer().selectProjectMenuItem(this._getCurrentProjectContainer().projectContainerHierarchyListItemHandler.getListItemIdByName(menuItemName), isFromBrowserAction);
          });  
        } 
    }

    _processProjectSwitcherPortals(respData) {
        this.navHandler.projectSwitcherPortals = Array.from(respData);
    }

    handleProjectRendering(portalName) {
        //If true, we show the project switcher component.
        if (this._isShowProjectSwitcher(portalName)) {
            this._showProjectTitleBar();
        } else {
            this._getCurrentProjectContainer().handleShowProjectTitleBarState(false);
        }
    }

    _isShowProjectSwitcher(portalName) {
    return this.navHandler.projectSwitcherPortals.includes(portalName);
    }

    _showProjectTitleBar() {
        this._getCurrentProjectContainer().handleShowProjectTitleBarState(true);
    }

    handleShowRecentProjectLink(currentProjectId, priorProjectId) {
        this._getCurrentProjectContainer().handleShowRecentProjectLinkState(this.shouldShowRecentProject(currentProjectId, priorProjectId));
    }

    shouldShowRecentProject(currentProjectId, priorProjectId) {
    //If prior project id is greater than 1, we should display the recent project link if we are default project
    return  !this._getCurrentProjectContainer().isProjectLevelContext(currentProjectId) &&
        this._getCurrentProjectContainer().isProjectLevelContext(priorProjectId);
    }

    isCurrentProjectAtProjectLevelContext() {
        return this._getCurrentProjectContainer().isCurrentProjectAtProjectLevelContext();
    }

    _getCurrentProjectContainer() {
        return this.projectContainer.current;
    }
}