let userSession = {active:false};

export async function checkUserSessionStatus(){
    await fetch('../../SessionExpiration')
    .then((contextResp) => { 
        return contextResp.text()})
    .then(sessionState => {
        userSession.active = sessionState==='active'
    });
}

export function userHasActiveSession() {
    return userSession.active;
}