export class ActiveMenuItemHandler {

    constructor(hierarchyListItemHandler) {
        this.hierarchyListItemHandler = hierarchyListItemHandler;
        this.currentActiveMenuId = null;
        this.currentActiveTopParentMenuId = null
    }

    handle(menuId) {
        //First remove menu active classes
        var triMenuItemElement;
        var triMenuItemTopParentElement;
        if (this.currentActiveMenuId != null) {
        triMenuItemElement = document.getElementById(this.currentActiveMenuId);
        if (triMenuItemElement.classList.contains("sideNavMenuItemWithChildren")) {
            triMenuItemElement.parentElement.parentElement.parentElement.classList.remove("triMenuItemActiveBackground");
            triMenuItemElement.parentElement.parentElement.classList.remove("triMenuItemActiveBackground");
        } else {
            triMenuItemElement.classList.remove("triMenuItemActiveBackground");
        }
        triMenuItemElement.classList.remove("triMenuItemActive");
        }
        if (this.currentActiveTopParentMenuId != null) {
        triMenuItemTopParentElement = document.getElementById(this.currentActiveTopParentMenuId);
        if (triMenuItemTopParentElement.classList.contains("sideNavMenuItemWithChildren")) {
            triMenuItemTopParentElement.parentElement.parentElement.parentElement.classList.remove("triMenuItemTopParentActiveBackground");
        }
        triMenuItemTopParentElement.classList.remove("triMenuItemTopParentActive");
        }

        //Add active to selected menu and top parent
        this.currentActiveMenuId = menuId;
        triMenuItemElement = document.getElementById(this.currentActiveMenuId);
        if (triMenuItemElement.classList.contains("sideNavMenuItemWithChildren")) {
        triMenuItemElement.parentElement.parentElement.parentElement.classList.add("triMenuItemActiveBackground");
        triMenuItemElement.parentElement.parentElement.classList.add("triMenuItemActiveBackground");
        } else {
        triMenuItemElement.classList.add("triMenuItemActiveBackground");
        }
        triMenuItemElement.classList.add("triMenuItemActive");
            
        this.currentActiveTopParentMenuId = this.hierarchyListItemHandler.getListItemById(menuId).topParentMenuId;
       
        if (this.currentActiveTopParentMenuId != null) {
            triMenuItemTopParentElement = document.getElementById(this.currentActiveTopParentMenuId);
            if (triMenuItemTopParentElement.classList.contains("sideNavMenuItemWithChildren")) {
                triMenuItemTopParentElement.parentElement.parentElement.parentElement.classList.add("triMenuItemTopParentActiveBackground");
            }
            triMenuItemTopParentElement.classList.add("triMenuItemTopParentActive");
        }
    } 
}