import {NavUrlHandler} from "../handlers/NavUrlHandler";
import { AppMsg } from "../utils";

export class NavButtonClickService {

    constructor(projectContainer) {
        this.projectContainer = projectContainer;
    }

    selectProjectMenuItem(id, isFromBrowserAction) {
        this.navButtonClick(id, this.projectContainer.projectContainerHierarchyListItemHandler, isFromBrowserAction);
    }

    navButtonClick(menuId, hierarchyListItemHandler, isFromBrowserAction) {
        
        //Retrieve valid menu item.
        let menuItem = this.projectContainer.props.handleValidMenuItem(menuId, hierarchyListItemHandler);

        //Handle url and history name
        let navUrlHandler = new NavUrlHandler(menuItem, this.projectContainer.state.currentProjectId);
        navUrlHandler.handle();
        let menuItemUrl = navUrlHandler.getMenuItemUrl();
        let historyName = navUrlHandler.getHistoryName();

        let triNavigationSrc = this.projectContainer.state.triNavigationSrc;
        let sitemapMenuItemsToDisplay = this.projectContainer.state.sitemapMenuItemsToDisplay;
        let sitemapMenuItemIdsToDisplay = this.projectContainer.state.sitemapMenuItemIdsToDisplay;

        //Page title
        document.title = menuItem.content.value+AppMsg.getMessage(AppMsg.APP_PAGE_TITLE);
        
        //Is this a nav page.
        if (menuItem.isNavPage) {

            triNavigationSrc = '';

            //Check if this is the sitemap
            if (menuItemUrl.startsWith('/html/en/default/platform/mainpagepopout/mainpagepopout.jsp#name=sysSiteMap')) {
                sitemapMenuItemsToDisplay = hierarchyListItemHandler.getSiteMapMenuItems();
            } else {
                //Standard nav page
                sitemapMenuItemsToDisplay = menuItem.children;
            }
            sitemapMenuItemIdsToDisplay = hierarchyListItemHandler.getListItemIds();
        
        } else {
            //Set the iFrame source.
            triNavigationSrc = this.projectContainer.props.tririgaContextUrl + menuItemUrl;
        }

        //Handle nav click button state
        this.projectContainer.handleNavButtonClickState(menuItem, triNavigationSrc, sitemapMenuItemsToDisplay, sitemapMenuItemIdsToDisplay);

        //Browser history
        if (!isFromBrowserAction) {
            //Set browser history for routing purposes.
            var stateObj = { title: menuItem.name, url: historyName }; 
            window.history.pushState(stateObj, stateObj.title, stateObj.url);  
        }
    }
}