let navConfig = {};

export async function getNavConfigAndUserData() {
    await fetch('../../ui/navContext/properties')
    .then((contextResp) => contextResp.json()) 
    .then(contextProps => {
        navConfig.isShowPreferencesLink = contextProps.isShowPreferencesLink;
        navConfig.isShowHelpAndSupportLinks = contextProps.isShowHelpAndSupportLinks;
        navConfig.isShowAboutLink = contextProps.isShowAboutLink;
        navConfig.projectPickerQueryId = contextProps.projectPickerQueryId;
        navConfig.isCompanyLevelAccessAllowed = contextProps.isCompanyLevelAccessAllowed;
        navConfig.isRTL = contextProps.isLanguageMirrored;
        navConfig.userLanguageId = contextProps.userLanguageId;
        navConfig.isForceLogOutOnNoCompanyLevelAccess = contextProps.isForceLogOutOnNoCompanyLevelAccess;
        navConfig.projectId = contextProps.projectId;
        navConfig.projectName = contextProps.projectName;
        navConfig.priorProjectId = contextProps.priorProjectId;
        navConfig.priorProjectName = contextProps.priorProjectName;
    });
  }
  
  export function getNavConfigData() {
    return navConfig;
  }