import messages from "./messages/messages.json";
import {
  getTranslatedMessages,
  getTriAppConfig,
} from "@tririga/tririga-react-components";
import { NavConfig } from "../utils"
 
const VERSION = process.env.REACT_APP_VERSION;

const baseMessages = {
  ...messages,
};

let appMessages = null;

export const APP_ABOUT = "APP_ABOUT";
export const APP_HOME = "APP_HOME";
export const APP_HELP = "APP_HELP";
export const APP_LOGOUT = "APP_LOGOUT";
export const APP_TITLE = "APP_TITLE";
export const APP_NO_COMPANY = "APP_NO_COMPANY";
export const USER_USER = "USER_USER";
export const USER_PROFILE = "USER_PROFILE";
export const USER_MANAGE = "USER_MANAGE";
export const PROJ_SWITCH = "PROJ_SWITCH";
export const PROJ_RECORD = "PROJ_RECORD";
export const PROJ_RECENT = "PROJ_RECENT";
export const PROJ_SEARCH = "PROJ_SEARCH";
export const PROJ_DETAILS = "PROJ_DETAILS";
export const APP_PAGE_TITLE = "APP_PAGE_TITLE";
export const PROJ_EXPAND_MENU = "PROJ_EXPAND_MENU";
export const PROJ_COLLAPSE_MENU = "PROJ_COLLAPSE_MENU";
export const EXPAND = "EXPAND";
export const CLOSE = "CLOSE";
export const NO_SESSION_HEADING = "NO_SESSION_HEADING";
export const NO_SESSION_BODY = "NO_SESSION_BODY";
export const NO_SESSION_LOGIN = "NO_SESSION_LOGIN";


export function getMessage(label) {
  return appMessages[label];
}

export async function initMessages() {
  if (process.env.NODE_ENV==='development') {
      appMessages = baseMessages;
  } else if (!appMessages) {
    try {
      appMessages = await getTranslatedMessages(
        baseMessages,
        getTriAppConfig().appExposedName + VERSION,
        NavConfig.getNavConfigData().userLanguageId
      );
      
      localStorage.setItem("triPlatformNavMessages",JSON.stringify(appMessages));
    } catch (error) {
      alert(
        'Unable to get translations, defaulting to English -' + error
      );
      appMessages = baseMessages;
    }
  }
}