import React from 'react';
import ReactDOM from 'react-dom';
import NavMain from './nav/NavMain';
import {AppMsg, NavConfig, UserSession} from './utils';
import SessionExpired from "./session/SessionExpired";
import * as serviceWorker from './serviceWorker';
import {
    fetchTriAppConfig
} from "@tririga/tririga-react-components";

async function initAndRenderApp() {

    await UserSession.checkUserSessionStatus();

    if(UserSession.userHasActiveSession()){
        // get application information
        await fetchTriAppConfig();
        
        // get nav config and user information
        await NavConfig.getNavConfigAndUserData();
        
        // initialize static messages
        await AppMsg.initMessages();

        // render the application
        renderApp();
    }
    else{
        renderSessionExpired();
    }
}

async function renderApp() {
    ReactDOM.render(<NavMain />, document.getElementById('root'));

    // If you want your app to work offline and load faster, you can change
    // unregister() to register() below. Note this comes with some pitfalls.
    // Learn more about service workers: http://bit.ly/CRA-PWA
    serviceWorker.unregister();
}

async function renderSessionExpired(){
    ReactDOM.render(<SessionExpired />, document.getElementById('root'));
}

initAndRenderApp();