import React, { Component } from "react";
import UserAvatar from "@carbon/icons-react/lib/user--avatar/20";
import Home from "@carbon/icons-react/lib/home/20";
import ReportData from "@carbon/icons-react/lib/report--data/20";
import RequestQuote24 from "@carbon/icons-react/lib/request-quote/20"
import Tools from "@carbon/icons-react/lib/tools/20";
import Workspace from "@carbon/icons-react/lib/workspace/20";
import DocumentTask from "@carbon/icons-react/lib/document--tasks/20";
import Document from "@carbon/icons-react/lib/document/20";
import Portfolio from "@carbon/icons-react/lib/portfolio/20";
import Box from "@carbon/icons-react/lib/box/20";
import InventoryManagement from "@carbon/icons-react/lib/inventory-management/20";
import Earth from "@carbon/icons-react/lib/earth/20";
import Apps from "@carbon/icons-react/lib/apps/16";
import Network from "@carbon/icons-react/lib/network--2/20";
import { ReactComponent as MPRToggle } from '../icons/3D-MPR-Toggle.svg';
import { ReactComponent as StackedScrolling1 } from '../icons/stacked-scrolling--1.svg';
import { HierarchyListItemHandler } from "../handlers/HierarchyListItemHandler";
import { NavRoutingHandler } from "../handlers/NavRoutingHandler";
import { FaviconHandler } from "../handlers/FaviconHandler";
import { ActiveMenuItemHandler } from "../handlers/ActiveMenuItemHandler";
import { ProjectContextService } from "../services/ProjectContextService";
import { AppMsg, NavConfig } from "../utils";

import {
  HeaderContainer,
  Header,
  SideNav
} from "tririga-carbon-addons-iot-react";
import ProjectContainer from "../project/ProjectContainer.js";
import UserProfile from "../user/UserProfile.js";
import { about } from "../about/About.js";
import { help } from "../help/Help.js";
import "./NavMain.scss";

const COMPANY_LEVEL_PROJECT_ID = 1;
const SUB_CAT_NOT_NECESSARY = -1;
const MENU_ITEM_NAME_NOT_NECESSARY = null;
const NOT_FROM_BROWSER_ACTION = false;

const SUPPRESS_ICON = true;
const DO_NOT_SUPPRESS_ICON = false;

class NavMain extends Component {

  constructor(props) {
    super(props)
    this.state = {
      menuItems: []
    }
    this.tririgaContextUrl = this._getContextUrl();
    this.sideNavRefDiv = React.createRef();
    this.projectContainerDivRef = React.createRef();
    this.projectContainer = React.createRef();
    this.switchProject = this.switchProject.bind(this);
    this.sitemapMenuItemButtonClick = this.sitemapMenuItemButtonClick.bind(this);
    this.handleNavFrameSource = this.handleNavFrameSource.bind(this);
    this.handleValidMenuItem = this.handleValidMenuItem.bind(this);
    this.hierarchyListItemHandler = new HierarchyListItemHandler();
    this.projectContextService = new ProjectContextService(this, this.projectContainer);
    this.projectSwitcherPortals = [];
    this.projectSideNav = null;
    this.activeMenuItemHandler = new ActiveMenuItemHandler(this.hierarchyListItemHandler);
  }

  componentDidMount() {

    var contextProps = NavConfig.getNavConfigData();

    //Nav Items
    fetch('../../api/v1/navCollection/currentUserDefaultMenu')
      .then((resp) => resp.json())
      .then(data => {
        let navItems = data.navItems;
        this._handleSideNavLinks(navItems, DO_NOT_SUPPRESS_ICON);

        //Site map items
        this.hierarchyListItemHandler.handleFromMain(navItems);

        this.setState({
          menuItems: navItems
        })

        //Handle project context properties
        this.projectContextService.handleProjectContextProperties(contextProps.projectId,
          contextProps.projectName,
          contextProps.priorProjectId,
          contextProps.priorProjectName);

        this.projectContextService.handleShowProjectMenu(contextProps.projectId);

        //Detect browser back, forward, and refresh.
        window.addEventListener('popstate', this._handleNavigateRouteOnBrowserAction.bind(this));

        //Handle any routing
        this._handleNavigateRoute();
        this._handleRTL();

      });

    //Handle project switcher menu
    this.projectContextService.handleProjectSwitcherMenu();

    this._handleFavicon();
  }

  _handleRTL() {
    if (NavConfig.getNavConfigData().isRTL) {
      document.getElementsByTagName('html')[0].setAttribute("dir", "rtl");
    }
  }
  componentWillUnmount() {
    //Clear any listeners
    window.removeEventListener('popstate', this._handleNavigateRouteOnBrowserAction.bind(this));
  }

  //favicon handling
  _handleFavicon() {
    new FaviconHandler().handle();
  }

  switchProjectAndSelectProjectMenuItem(projectId, menuItemName, isFromBrowserAction) {
    this.switchProject(SUB_CAT_NOT_NECESSARY, projectId, menuItemName, isFromBrowserAction);
  }

  //Switch to different project
  switchProject(subCatId, projectId, menuItemName, isFromBrowserAction, navHandleCallback) {
    this.projectContextService.switchProject(subCatId, projectId, menuItemName, isFromBrowserAction, navHandleCallback);
  }

  _getMenuItems() {
    if (this.state.menuItems.length < 1) {
      return [];
    }
    let menuItems = this.state.menuItems;
    return menuItems;
  }

  _handleSideNavLinks(menuItems, isSuppressIcon) {
    for (var i = 0; i < menuItems.length; i++) {
      if (menuItems[i].metaData) {
        let id = menuItems[i].id;
        let name = menuItems[i].name;
        if (menuItems[i].childContent) {
          menuItems[i].linkContent = <div id={menuItems[i].id}
            tabindex="0" role="listitem" aria-label={menuItems[i].content}
            onKeyUp={(event) => {
              this._navButtonClickFromEnterKey(event, id);
            }}
            onKeyDown={(event) => {
              if (event.keyCode === 13) {
                //Prevent default so expand/collapse does not happen on enter click.
                event.preventDefault();
              }
            }}
            onClick={(event) => {
              this._navButtonClickFromCompanyContext(id);
              //Stop propagation so expand/collapse does not happen.
              event.stopPropagation();
            }}
            className="sideNavMenuItemWithChildren">
            <div className="sideNavMenuItemWithChildrenLabel">
              {menuItems[i].content}
            </div>
          </div>
        } else {
          menuItems[i].metaData.onClick = () => { this._navButtonClickFromCompanyContext(id) };
        }

        //Getting icons for NavItems
        if (isSuppressIcon) {
          menuItems[i].icon = '';
        } else {
          let iconPath = menuItems[i].icon;
          if (iconPath === "") {
            menuItems[i].icon = this._getIcon(name);
          }
          else {
            //Path of icon as per Tririga
            let path = this._getContextUrl() + '/getCompanyFile.jsp?fileLoc=' + iconPath;
            let variable = () => {
              return (<img class='icon-color' src={`${path}`} width="16px" innerHeight="16px" />)
            }
            menuItems[i].icon = variable;
          }
        }
      }

      //Iterate through children
      if (menuItems[i].childContent) {
        this._handleSideNavLinks(menuItems[i].childContent, SUPPRESS_ICON)
      }
    }
  }

  _ensureCompanyLevel(navHandleCallback) {
    if (this.projectContextService.isCurrentProjectAtProjectLevelContext()) {
      //Always force clicks from side nav to company level. 
      //Company Level has project id of 1.
      this.switchProject(SUB_CAT_NOT_NECESSARY,
        COMPANY_LEVEL_PROJECT_ID,
        MENU_ITEM_NAME_NOT_NECESSARY,
        NOT_FROM_BROWSER_ACTION,
        navHandleCallback);
    } else {
      if (navHandleCallback) {
        navHandleCallback();
      }
    }
  }

  //Handle enter key click
  _navButtonClickFromEnterKey(event, menuId) {
    //keyCode 13 is the enter key
    if (event.keyCode === 13) {
      this._navButtonClickFromCompanyContext(menuId);
    }
  }

  //Clicks from company level sitemap
  sitemapMenuItemButtonClick(menuId) {
    this._navButtonClickFromCompanyContext(menuId);
  }

  _navButtonClickFromCompanyContext(menuId, isFromBrowserAction) {
    //Handle active menu item style
    this.activeMenuItemHandler.handle(menuId);

    //Ensure company level on any side nav click
    this._ensureCompanyLevel(() => {
      this.navButtonClick(menuId, this.hierarchyListItemHandler, isFromBrowserAction);
    });
  }

  //Nav button click from main side nav
  navButtonClick(menuId, hierarchyListItemHandler, isFromBrowserAction, projectId) {

    //Retrieve valid menu item.
    let menuItem = this.handleValidMenuItem(menuId, hierarchyListItemHandler);
    if (menuItem.portalName != null && !projectId) {
      this.projectContextService.handleProjectRendering(menuItem.portalName);
    }
    this.projectContainer.current.navButtonClick(menuId, hierarchyListItemHandler, isFromBrowserAction);
  }

  //Currently used by User Profile to set source.
  handleNavFrameSource(menuItemUrl) {
    this.projectContainer.current.handleNavFrameSource(menuItemUrl);
  }

  //Function for getting default icons for Navigation Items.
  _getIcon(linkContent) {
    let selectedIcon;
    switch (linkContent) {
      case '': selectedIcon = Home;
        break;
      case 'Builder+-+My+Reports': selectedIcon = ReportData;
        break;
      case 'Landing+Page+-+Requests': selectedIcon = RequestQuote24;
        break;
      case 'Landing+Page+-+Projects': selectedIcon = StackedScrolling1;
        break;
      case 'Landing+Page+-+Tasks': selectedIcon = DocumentTask;
        break;
      case 'Landing+Page+-+Space': selectedIcon = Workspace;
        break;
      case 'Landing+Page+-+Maintenance': selectedIcon = Tools;
        break;
      case 'Landing+Page+-+Sustainability': selectedIcon = Earth;
        break;
      case 'Landing+Page+-+Inventory': selectedIcon = MPRToggle;
        break;
      case 'Landing+Page+-+Contracts': selectedIcon = Document;
        break;
      case 'Landing+Page+-+Procurement': selectedIcon = InventoryManagement;
        break;
      case 'Landing+Page+-+Portfolio': selectedIcon = Portfolio;
        break;
      case 'Landing+Page+-+Tools': selectedIcon = Box;
        break;
      case 'sysSiteMap': selectedIcon = Network;
        break;
      default: selectedIcon = Apps;
    }
    return selectedIcon;
  }

  _getContextUrl() {
    //return document.getElementById("triContextUrl").value;
    let triContextUrl = "../..";
    if (process.env.NODE_ENV === 'development') {
      triContextUrl = process.env.REACT_APP_DEV_HOST;
    };
    return triContextUrl;
  }

  _getHeaderActionItems() {
    let actionItemsArray = [];

    //If SHOW_ABOUT_LINK property in TRIRIGAWEB.properties is TRUE
    if (NavConfig.getNavConfigData().isShowAboutLink) {
      actionItemsArray.push(about(this.tririgaContextUrl));
    }

    //If SHOW_HELP_AND_SUPPORT_LINKS property in TRIRIGAWEB.properties is TRUE
    if (NavConfig.getNavConfigData().isShowHelpAndSupportLinks) {
      actionItemsArray.push(help(this.tririgaContextUrl));
    }

    actionItemsArray.push(
      {
        label: AppMsg.getMessage(AppMsg.USER_USER),
        btnContent: <UserAvatar fill="white" description="Icon" />,
        childContent: [
          {
            content: (
              <UserProfile {...this.state} tririgaContextUrl={this.tririgaContextUrl} handleNavFrameSource={this.handleNavFrameSource} />
            ),
            metaData: {
              element: 'div'
            }
          },
          {
            metaData: {
              title: AppMsg.getMessage(AppMsg.APP_LOGOUT),
              target: '_blank',
              rel: 'noopener noreferrer',
              element: 'a',
              style: { textIndent: '16px' },
              onClick: () => { this._logOutClicked() },
              onKeyDown: (event) => { this._logOutFromEnterKey(event) }
            },
            content: AppMsg.getMessage(AppMsg.APP_LOGOUT),
          }

        ]
      }
    );

    return actionItemsArray;

  }

  _logOutClicked() {
    fetch('../../ui/navContext/logOut')
      .then((resp) => resp.json())
      .then(data => {
        if (data.logoutURL) {
          window.location.href = `${window.location.origin}` + data.logoutURL;
        }
      });
  }

  //Handle enter key click
  _logOutFromEnterKey(event) {
    //keyCode 13 is the enter key
    if (event.keyCode === 13) {
      this._logOutClicked();
    }
  }

  handleValidMenuItem(menuId, hierarchyListItemHandler) {
    let menuItem = hierarchyListItemHandler.getListItemById(menuId);
    if (menuItem == null) {
      menuId = hierarchyListItemHandler.getListItemIdByName("");
      menuItem = hierarchyListItemHandler.getListItemById(menuId);
    }
    return menuItem;
  }

  _handleNavigateRouteOnBrowserAction() {
    this._handleNavigateRoute(true);
  }

  _handleNavigateRoute(isFromBrowserAction) {
    //Route handling
    let navRoutingHandler = new NavRoutingHandler(this, isFromBrowserAction);
    navRoutingHandler.handle();
  }


  render() {
    return (

      <main className="app">
        {//Don't render until menu items exist.
          this.state.menuItems.length === 0 ?
            <div className="bx--header" style={{ height: '5px' }} /> :
            <HeaderContainer
              render={({ isSideNavExpanded, onClickSideNavExpand }) => (
                <>
                  <Header
                    actionItems={this._getHeaderActionItems()}
                    appName={AppMsg.getMessage(AppMsg.APP_TITLE)}

                    isSideNavExpanded={isSideNavExpanded}
                    onClickSideNavExpand={onClickSideNavExpand}
                  />

                  <div ref={this.sideNavRefDiv} style={{ height: '100%', paddingTop: '48px' }}>
                    {NavConfig.getNavConfigData().isCompanyLevelAccessAllowed ?


                      <SideNav
                        links={this._getMenuItems()}

                        isSideNavExpanded={isSideNavExpanded}
                        onClickSideNavExpand={onClickSideNavExpand}

                      />

                      : null}
                    <div className="iot--main-content">
                      <div ref={this.projectContainerDivRef} className="projectContainerDiv">
                        <ProjectContainer ref={this.projectContainer}
                          {...this.state}
                          switchProject={this.switchProject}
                          projectSideNav={this.projectSideNav}
                          handleValidMenuItem={this.handleValidMenuItem}
                          tririgaContextUrl={this.tririgaContextUrl}
                          sitemapMenuItemButtonClick={this.sitemapMenuItemButtonClick}
                        />
                      </div>
                    </div>
                  </div>

                </>
              )}
            />
        }
      </main>
    );
  }
}

export default NavMain;


