import {
    isIPhone13,
    isIPad13,
    isIPod13,
    isIOS,
    isAndroid
  } from "react-device-detect";

export class FaviconHandler {

    handle() {
        let link = document.querySelector('link[rel="shortcut icon"]') || document.querySelector('link[rel="icon"]');
        if (!link) {
            link = document.createElement('link');
            link.id = 'favicon';
            link.rel = 'shortcut icon';
            document.head.appendChild(link);
        }
        //Default favicon
        link.href =  process.env.PUBLIC_URL +'/favicon/tririga-favicon-gradient.svg';
	    console.log("Public URL :"+process.env.PUBLIC_URL);
	    if (isAndroid) {
	      link.rel = "shortcut icon";
	      link.href = process.env.PUBLIC_URL + '/favicon/tririga-favicon-196px.png';
	    }
	    if (isIOS) {
	      link.rel = "apple-touch-icon";
	      link.href = process.env.PUBLIC_URL + '/favicon/tririga-favicon-180px.png';
	    }
	    if (isIPhone13) {
	      link.rel = "apple-touch-icon";
	      link.href = process.env.PUBLIC_URL + '/favicon/tririga-favicon-120px.png';
	    }
	    if (isIPad13) {
	      link.rel = "apple-touch-icon";
	      link.href = process.env.PUBLIC_URL + '/favicon/tririga-favicon-167px.png';
	    }
	    if (isIPod13) {
	      link.rel = "apple-touch-icon";
	      link.href = process.env.PUBLIC_URL + '/favicon/tririga-favicon-152px.png';
	    }
    }
}