import React, { Component } from "react";
import UserAvatar from 'react-avatar';
import {
  Button,
  Card
} from "tririga-carbon-addons-iot-react";
import "../nav/NavMain.scss";
import "./UserProfile.scss";
import { AppMsg, NavConfig } from "../utils";

const size = 'SMALLWIDE';
const COMPANY_LEVEL_PROJECT_ID = 1;

class UserProfile extends Component {

  constructor(props) {
    super(props)
    this.state = {
      fullName: '',
      email: '',
      image: ''
    }
  }

  componentDidMount() {
    fetch('../../api/v1/query/dataByQueryName?moduleName=triPeople&boName=My%20Profile&queryName=triPlatformNavigation-CurrentUserDetails')
      .then((resp) => resp.json()) 
      .then(respData => {
        this._processUserData(respData);
        this._handleManageAccountDiv();
      });
     
  }

  _handleManageAccountDiv() {
    var manageAccountDiv = document.getElementById('manageAccountDiv');
    if (NavConfig.getNavConfigData().isShowPreferencesLink) {
      manageAccountDiv.style.display = '';
    } else {
      manageAccountDiv.style.display = 'none';
    }
  }

  _processUserData(respData) {
    let data = respData.data;
    this.setState ({
      fullName: data[0]['0-General-PeopleFullName'],
      email: data[0]['0-RecordInformation-triEmailTX'],
      image: data[0]['0-RecordInformation-triImageIM'],
    });
  }

  //Set the User Profile Icon default/specific user image
  _setUserImage(userImage,userFullName){
 
    if(userImage){
      let path = this.props.tririgaContextUrl + '/getCompanyFile.jsp?fileLoc=' + userImage
      return(<UserAvatar size="32" src={`${path}`}  round={true} description="Icon"></UserAvatar>)
      }
    else{
      let customName = this._userIconName(userFullName);
      return(<UserAvatar className="style-usericon" name={customName} fgColor="#000000" size="32" textSizeRatio={1.75} color="white"  round={true} description="Icon"></UserAvatar>)
          }
  }

  //If the User Full Name has more than two words customize it so as to get appropriate initials for default user icon
  _userIconName(userFullName){
    let fullNameArray = userFullName.split(' ');
    let name = fullNameArray[0] + " " + fullNameArray[fullNameArray.length -1];
    return name;
  }

  _openUserProfile() {
    this.props.handleNavFrameSource(this._handleMyProfileSrc());

    //Set browser history for routing purposes.
    let userProfileHistoryName = '../tririga#sysMyProfile';
    if (this.props.currentProjectId > COMPANY_LEVEL_PROJECT_ID) {
      userProfileHistoryName = userProfileHistoryName + '&project=' + this.props.currentProjectId;
    }
    var stateObj = { title: 'sysMyProfile', url: userProfileHistoryName }; 
    window.history.pushState(stateObj, stateObj.title, stateObj.url);  
  }

  _handleMyProfileSrc() {
    let myProfileSrc = '/html/en/default/platform/mainpagepopout/mainpagepopout.jsp#name=sysMyProfile';
    if (this.props.currentProjectId > COMPANY_LEVEL_PROJECT_ID) {
      myProfileSrc = myProfileSrc + '&project=' + this.props.currentProjectId;
    }
    return myProfileSrc;
  }

  //Handle enter key click
  _openUserProfileFromEnterKey(event) {
    //keyCode 13 is the enter key
    if (event.keyCode === 13) {
      this._openUserProfile();
    }
  }

  render() {
    return (
        <div>
            <Card
          title={
            'title',
            AppMsg.getMessage(AppMsg.USER_PROFILE)
          }
          id="user-profile"
          size={size}
          isLoading={false}
          isEmpty={false}
          isEditable={false}
          isExpanded={false}
          children={childSize => (
          <div>
            <div className="userProfileContent">
          <span className='icon'>{this._setUserImage(this.state.image,this.state.fullName)}</span>
                <span class='text'><div class='full-name'>{this.state.fullName}</div><div class='email'>{this.state.email}</div></span>                
            </div>
            <div id='manageAccountDiv' className="userManageAccount">
              <Button kind="secondary" size="small" onClick={() => {this._openUserProfile()}} 
                        onKeyDown={(event) => {this._openUserProfileFromEnterKey(event)}}>
                {AppMsg.getMessage(AppMsg.USER_MANAGE)}
              </Button>
            </div>
           </div>     
          )}
        />
        </div>
    );
  }
}

export default UserProfile;